  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.chapter1, .chapter2, .chapter3,.chapter4,.chapter5,.chapter6 {
  
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  text-align: center;
}

input[type=text] {
  width: 65%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.button {
  border: none;
  padding: 16px 32px;
  text-align: center;
  font-size: 16px;
  margin: 4px 2px;
}

.button5 {
  background-color: black;
  color: white;
  border: 2px solid #555555;
}

.button5:hover {
  background-color: #555555;
  color: white;
}
font.text1{
  font-size: 2rem;
  padding: 5px;
  color: white;
}

